body {
  font-family: Arial, sans-serif;
  background-color: #ecf0f1;
  color: #2c3e50;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #3498db;
  color: #fff;
  padding: 20px;
  text-align: center;
}

.navbar {
  display: flex;
  justify-content: space-around;
  background-color: #2ecc71;
  padding: 10px;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  padding: 10px;
}

.navbar a:hover {
  background-color: #e74c3c;
}

.main-content {
  padding: 20px;
}

.footer {
  background-color: #3498db;
  color: #fff;
  text-align: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.form-container {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-group button {
  background-color: #3498db;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

.form-group button:hover {
  background-color: #2980b9;
}

.calendar {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.calendar h3 {
  margin-bottom: 15px;
}
